body {
  background-color: white;
}

.component {
  font-family:arial;
}

.titre {
font-family: 'Montserrat', sans-serif;
  font-size:30px;

}

.display-table{
    display: table;
    table-layout: fixed;
    width:100%;
}

.display-cell{
    display: table-cell;
    vertical-align: middle;
    float: none;
}

.contenu {
  max-width:1000px;
  margin:auto;
}

.column-titles {
  font-family: 'Raleway', sans-serif;
  font-weight:800;
  text-transform:uppercase;
  border:1px #000 solid;
  text-align:center;
  }

  .smallinput {
      font-size:12px;
      line-height:12px;
      width:90%;
      margin:5px 0px 5px 0px;
  };

alert-form {
        color:darkred;
        font-weight:800;
        font-size:20px;
        text-align:justify;

}
.centered {
    text-align:center;
}
@media screen and (max-width: 767px) {
    body {
        font-size: 0.9em;
    }
}

/* sm */
@media screen and (min-width: 768px) {
    body {
        font-size: 1em;
    }
}

/* md */
@media screen and (min-width: 992px) {
    body {
        font-size: 1.1em;
    }
}

/* lg */
@media screen and (min-width: 1200px) {
    body {
        font-size: 1.3em;
    }
}
